.card {
  position: relative;
  cursor: pointer;
  min-height: 386px;
  min-width: 0;
}


.card__front,
.card__back {
  width: 100%;
  height: 100%;
  word-wrap: break-word;
}

.card__front img {
  display: block;
  height: 386px;
  width: auto;
  object-fit: cover;

  @include vp-1023 {
    height: auto;
    width: 437px;
  }

  @include vp-767 {
    height: auto;
    width: 717px;
    max-height: 386px;
  }
}

.card__front {
  background-color: $color-tangaroa;
}

.card__front-title {
  margin: 0;
  position: absolute;
  bottom: 30px;
  left: 11.5%;
  max-width: 80%;
  max-height: 85%;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  color: $color-alice-blue;
  overflow: hidden;

  @include vp-1023 {
    bottom: 35px;
  }

  @include vp-767 {
    bottom: 25px;
  }
}

.card__back {
  display: none;
  background-color: $color-tangaroa;
  color: $color-alice-blue;
  padding: 28px 25px 15px 25px;

  @include vp-1023 {
    padding: 35px 35px 10px 35px;
  }

  @include vp-1023 {
    padding: 35px 25px 10px 25px;
  }
}

.card__back-title {
  margin: 0;
  margin-bottom: 13px;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
}

.card__back-subtitle {
  margin: 0;
  margin-bottom: 1px;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
}

.card__back-text {
  display: block;
  margin-bottom: 11px;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: $color-quartz;
}

.card:hover>.card__front {
  display: none;
}

.card:hover>.card__back {
  display: block;
}
