.form {
  padding: 50px 0;
  min-height: 555px;
  width: auto;
  background-color: $color-quartz;
  background-image: url("../img/form-background-desktop.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  @media (min-width: $vp-1024) {
    @media (min-resolution: 2dppx) {
      background-image: url("../img/form-background-desktop-2x.jpg");
      min-height: 555px;
      width: auto;
    }
  }

  @include vp-1023 {
    min-height: 805px;
    background-image: url("../img/form-background-tablet.jpg");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/form-background-tablet-2x.jpg");
      min-height: 805px;
      width: auto;
    }
  }

  @include vp-767 {
    min-height: 760px;
    background-image: url("../img/form-background-mobile.jpg");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/form-background-mobile-2x.jpg");
      min-height: 760px;
      width: auto;
    }
  }

  @include vp-1023 {
    padding: 100px 0;
  }

  @include vp-767 {
    padding: 65px 0;
  }
}

.form__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 0 5.2%;

  @include vp-1023 {
    grid-template-columns: 1fr;
    padding: 0;
  }
}

.form__text-wrapper {
  align-self: center;

  @include vp-1023 {
    justify-self: center;
    padding-bottom: 60px;
  }

  @include vp-767 {
    padding-bottom: 40px;
  }
}

.form__text-wrapper h2 {
  margin: 0;
  margin-bottom: 30px;
  font-weight: 700;
  font-size: 40px;
  line-height: 60px;

  @include vp-1023 {
    font-size: 34px;
    line-height: 51px;
    margin-bottom: 25px;
    text-align: center;
  }

  @include vp-767 {
    font-size: 24px;
    line-height: 31px;
  }
}

.form__text-wrapper span {
  display: block;
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
  padding-right: 150px;

  br {
    display: none;
  }

  @include vp-1023 {
    text-align: center;
    padding: 0 150px;
  }

  @include vp-767 {
    font-size: 16px;
    line-height: 22px;
    padding: 0;

    br {
      display: block;
    }
  }
}

.form__block-wrapper {
  padding-left: 22.5%;

  @include vp-1023 {
    display: flex;
    padding-left: 0;
    justify-content: center;
  }
}
