@font-face {
  font-family: "Montserrat";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/montserrat-regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-weight: 500;
  font-style: normal;
  src: url("../fonts/montserrat-medium.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-weight: 600;
  font-style: normal;
  src: url("../fonts/montserrat-semi-bold.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-weight: 700;
  font-style: normal;
  src: url("../fonts/montserrat-bold.woff2") format("woff2");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011C40;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #011C40;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #011C40;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Montserrat", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1260px;
  margin: 0 auto;
  padding: 0 50px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.advantages {
  padding: 120px 0;
  background-color: #D8D8D9;
  background-image: url("../img/advantages-background-desktop.jpg");
  min-height: 562px;
  width: auto;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
@media (min-width: 1024px) and (min-resolution: 2dppx) {
  .advantages {
    background-image: url("../img/advantages-background-desktop-2x.jpg");
    min-height: 562px;
    width: auto;
  }
}
@media (max-width: 1023px) {
  .advantages {
    padding: 100px 0;
    min-height: 588px;
    background-image: url("../img/advantages-background-tablet.jpg");
  }
}
@media (max-width: 1023px) and (min-resolution: 2dppx) {
  .advantages {
    background-image: url("../img/advantages-background-tablet-2x.jpg");
    min-height: 588px;
    width: auto;
  }
}
@media (max-width: 767px) {
  .advantages {
    padding: 65px 0;
    min-height: 997px;
    background-image: url("../img/advantages-background-mobile.jpg");
  }
}
@media (max-width: 767px) and (min-resolution: 2dppx) {
  .advantages {
    background-image: url("../img/advantages-background-mobile-2x.jpg");
    min-height: 997px;
    width: auto;
  }
}

.advantages__title {
  margin: 0;
  margin-bottom: 60px;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  text-align: center;
}
@media (max-width: 1023px) {
  .advantages__title {
    font-size: 26px;
    line-height: 39px;
  }
}
@media (max-width: 767px) {
  .advantages__title {
    font-size: 24px;
    line-height: 36px;
    margin-bottom: 40px;
  }
}

.advantages__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
}
@media (max-width: 1023px) {
  .advantages__list {
    gap: 30px;
  }
}
@media (max-width: 767px) {
  .advantages__list {
    grid-template-columns: 1fr;
    gap: 30px;
  }
}

.advantages__list-item {
  position: relative;
  background-color: #F9FBFD;
  padding: 30px 30px 50px 30px;
}
@media (max-width: 1023px) {
  .advantages__list-item {
    padding: 26px 15px 25px 20px;
  }
}
@media (max-width: 767px) {
  .advantages__list-item {
    padding: 25px 15px 25px 20px;
  }
}

.advantages__list-item-title {
  margin: 0;
  margin-bottom: 20px;
  padding-top: 57px;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
}
.advantages__list-item-title br {
  display: none;
}
@media (max-width: 1023px) {
  .advantages__list-item-title br {
    display: inline;
  }
}
.advantages__list-item-title--ship::before {
  content: "";
  display: block;
  position: absolute;
  top: 30px;
  left: 30px;
  background-image: url("../img/svg/advantages-1.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 27px;
  height: 32px;
}
@media (max-width: 1023px) {
  .advantages__list-item-title--ship::before {
    top: 26px;
    left: 20px;
    width: 21px;
    height: 26px;
  }
}
.advantages__list-item-title--tick::before {
  content: "";
  display: block;
  position: absolute;
  top: 30px;
  left: 30px;
  background-image: url("../img/svg/advantages-2.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 38px;
  height: 32px;
}
@media (max-width: 1023px) {
  .advantages__list-item-title--tick::before {
    top: 26px;
    left: 20px;
    width: 27px;
    height: 26px;
  }
}
.advantages__list-item-title--zodiac::before {
  content: "";
  display: block;
  position: absolute;
  top: 30px;
  left: 30px;
  background-image: url("../img/svg/advantages-3.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 44px;
  height: 32px;
}
@media (max-width: 1023px) {
  .advantages__list-item-title--zodiac::before {
    top: 26px;
    left: 20px;
    width: 36px;
    height: 26px;
  }
}

.advantages__list-item p {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
}

.application-form {
  padding: 50px 12.4%;
  background: #011C40;
  box-shadow: 0px 4px 45px rgba(1, 28, 64, 0.15);
  width: 100%;
}
@media (max-width: 1023px) {
  .application-form {
    padding: 49px;
    width: 59.4%;
  }
}
@media (max-width: 767px) {
  .application-form {
    padding: 40px 38px;
    width: 100%;
  }
}

.application-form__fieldset {
  border: none;
  margin: 0;
  padding: 0;
}

.application-form__text {
  display: block;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  color: #F9FBFD;
  margin-bottom: 35px;
}
@media (max-width: 767px) {
  .application-form__text {
    line-height: 25px;
  }
}

.application-form__input input {
  width: 100%;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #011C40;
  margin-bottom: 20px;
  padding: 11px 16px;
  border: 2px solid transparent;
}
.application-form__input input::placeholder {
  color: rgba(1, 28, 64, 0.7);
}

.application-form__checkbox-wrapper label {
  position: relative;
  display: block;
  font-weight: 400;
  font-size: 10px;
  line-height: 13px;
  color: #F9FBFD;
  padding-left: 23px;
  margin-bottom: 35px;
}

.application-form__control-mark {
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
  left: 0;
  width: 18px;
  height: 18px;
  border: 2px solid transparent;
  background-color: #F9FBFD;
}

.application-form__checkbox-wrapper input:checked + .application-form__control-mark::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 9px;
  height: 7px;
  background-image: url("../img/svg/tick.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.application-form__checkbox-wrapper input:not(:disabled):hover + .application-form__control-mark {
  border-color: #2c39f2;
}

.application-form__checkbox-wrapper input:not(:disabled):focus + .application-form__control-mark {
  border-color: #2c39f2;
}

.application-form__checkbox-wrapper input:disabled + .application-form__control-mark,
.application-form__checkbox-wrapper input:disabled ~ .application-form__checkbox-text {
  opacity: 0.4;
}

.application-form input:hover {
  border-color: #2c39f2;
}
.application-form input:focus {
  border-color: #2c39f2;
  outline: none;
}
.application-form input:disabled {
  opacity: 0.4;
}

.btn {
  display: inline-flex;
  padding: 10px;
  color: #011C40;
  background-color: #F9FBFD;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.btn--card {
  margin-top: 12px;
}
.btn:hover, .btn:focus {
  background-color: #D8D8D9;
  opacity: 0.7;
}
.btn:active {
  background-color: #D8D8D9;
  opacity: 0.5;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}

.catalog {
  padding: 95px 0 120px 0;
}
@media (max-width: 1023px) {
  .catalog {
    padding: 100px 0 100px 0;
  }
}
@media (max-width: 767px) {
  .catalog {
    padding: 65px 0 65px 0;
  }
}

.catalog__title {
  margin: 0;
  margin-bottom: 60px;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  text-align: center;
}
@media (max-width: 1023px) {
  .catalog__title {
    font-size: 26px;
    line-height: 39px;
  }
}
@media (max-width: 767px) {
  .catalog__title {
    font-size: 24px;
    line-height: 36px;
    margin-bottom: 40px;
  }
}

.catalog__list {
  margin: 0;
  padding: 0 18px 0 18px;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
}
@media (max-width: 1023px) {
  .catalog__list {
    padding: 0 15px 0 15px;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 767px) {
  .catalog__list {
    padding: 0 10px 0 10px;
    grid-template-columns: 1fr;
  }
}

.card {
  position: relative;
  cursor: pointer;
  min-height: 386px;
  min-width: 0;
}

.card__front,
.card__back {
  width: 100%;
  height: 100%;
  word-wrap: break-word;
}

.card__front img {
  display: block;
  height: 386px;
  width: auto;
  object-fit: cover;
}
@media (max-width: 1023px) {
  .card__front img {
    height: auto;
    width: 437px;
  }
}
@media (max-width: 767px) {
  .card__front img {
    height: auto;
    width: 717px;
    max-height: 386px;
  }
}

.card__front {
  background-color: #011C40;
}

.card__front-title {
  margin: 0;
  position: absolute;
  bottom: 30px;
  left: 11.5%;
  max-width: 80%;
  max-height: 85%;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  color: #F9FBFD;
  overflow: hidden;
}
@media (max-width: 1023px) {
  .card__front-title {
    bottom: 35px;
  }
}
@media (max-width: 767px) {
  .card__front-title {
    bottom: 25px;
  }
}

.card__back {
  display: none;
  background-color: #011C40;
  color: #F9FBFD;
  padding: 28px 25px 15px 25px;
}
@media (max-width: 1023px) {
  .card__back {
    padding: 35px 35px 10px 35px;
  }
}
@media (max-width: 1023px) {
  .card__back {
    padding: 35px 25px 10px 25px;
  }
}

.card__back-title {
  margin: 0;
  margin-bottom: 13px;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
}

.card__back-subtitle {
  margin: 0;
  margin-bottom: 1px;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
}

.card__back-text {
  display: block;
  margin-bottom: 11px;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #D8D8D9;
}

.card:hover > .card__front {
  display: none;
}

.card:hover > .card__back {
  display: block;
}

.contacts {
  padding: 120px 0;
}
@media (max-width: 1023px) {
  .contacts {
    padding: 100px 0;
  }
}
@media (max-width: 767px) {
  .contacts {
    padding: 65px 0;
  }
}

.contacts__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
}
@media (max-width: 1023px) {
  .contacts__wrapper {
    gap: 30px;
  }
}
@media (max-width: 767px) {
  .contacts__wrapper {
    grid-template-columns: 1fr;
    gap: 43px;
  }
}

.contacts__map-wrapper {
  background-image: url("../img/map-desktop.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
